import React, { useEffect } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import AppOnePage from "./AppOne";
import AppTwoPage from "./AppTwo";
import GA4React from "ga-4-react";
import "./App.css";

const ga4react = new GA4React("G-QC2JW90KEH");

function App() {
  //using useEffect to track pageviews with ga4react
  useEffect(() => {
    //initialize ga4react
    ga4react
      .initialize()
      .then((res) => console.log(res, "Analytics Success."))
      .catch((err) => console.log(err, "Analytics Failure."))
      .finally(() => {
        //track pageview
        ga4react.pageview(window.location.pathname + window.location.search);
      });
  }, []);

  return (
    <div className="App">
      <header className="App-header">
        <Router>
          <a href="/appOne">AppOne</a>
          <br />
          <a href="/appTwo">AppTwo</a>
          <br />
          <br />

          <Routes>
            <Route exact path="/appOne">
              <AppOnePage />{" "}
            </Route>
            <Route exact path="/appTwo">
              <AppTwoPage />{" "}
            </Route>
          </Routes>
        </Router>
      </header>
    </div>
  );
}

export default App;
